@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600;700&display=swap);
.Button{display:inline-block;height:34px;width:100px;font-size:var(--fs-md);border-radius:var(--button-br);color:var(--button-color);background-color:var(--button-primary-bg);cursor:pointer}.Button__container{display:flex;align-items:center;justify-content:center;width:100%;height:100%;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Button:hover{background-color:var(--button-hover-bg)}.Button--disabled,.Button--disabled:hover{color:var(--button-disabled-color);background-color:var(--button-disabled-bg)}

.Header{background-color:var(--white);position:fixed;top:0;right:0;width:100%;z-index:9999;box-shadow:0 2px 4px 0 rgba(0,0,0,0.04)}.Header__container{background-color:red;height:60px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;grid-gap:0.2rem;gap:0.2rem}.Header__logo button{border:none;outline:none;background-color:transparent;display:flex;align-items:center;cursor:pointer}.Header__nav{display:flex;grid-gap:0.2rem;gap:0.2rem}@media (min-width: 768px){.Header__nav{grid-gap:1rem;gap:1rem}}

.Container{padding:0;width:100%;max-width:1170px;min-width:360px;margin:0 auto}

.Wrapper{width:100%;padding:0 15px 0 15px;box-sizing:border-box}@media (min-width: 768px){.Wrapper{padding:0 32px 0 32px}}@media (min-width: 1024px){.Wrapper{padding:0 60px 0 60px}}@media (min-width: 1170px){.Wrapper{padding:0}}

.Promo__poster{min-height:500px;width:100%;height:100vh;box-sizing:border-box;background-repeat:no-repeat;background-size:cover;background-position:center;color:var(--white);background-image:url(../../static/media/promo-360.f09745ec.png);font-weight:300;text-align:center;display:flex;flex-direction:column;align-items:center;justify-content:center}.Promo h1{margin:40px auto 0;font-size:var(--fs-h1);font-weight:var(--fw-normal);line-height:var(--lh-h1);max-width:350px;text-align:center}.Promo p{margin:0 auto;padding-top:21px;max-width:328px;font-size:var(--fs-md);font-weight:var(--fw-normal);line-height:var(--lh-md);padding-bottom:32px}@media (min-width: 768px){.Promo__poster{background-image:url(../../static/media/promo-768.78baa64c.png)}.Promo h1{max-width:450px}.Promo p{max-width:381px;padding-bottom:36px}}@media (min-width: 1024px){.Promo__poster{background-image:url(../../static/media/promo-1024.5fff95b2.png)}}

.Card{width:100%;height:254px;background-color:var(--white);color:var(--black);border-radius:var(--card-br);padding:20px;box-sizing:border-box;display:flex;flex-direction:column;align-items:center;font-size:var(--fs-md);line-height:var(--lh-md);text-align:center}.Card__photo{width:70px;height:70px;border-radius:70px;margin-bottom:20px}.Card__name{margin-bottom:20px}.Card__email,.Card__name{overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow-wrap:break-word}

.List{display:grid;grid-template-columns:repeat(1, 1fr);grid-gap:20px;gap:20px}@media (min-width: 768px){.List{grid-template-columns:repeat(2, 1fr);grid-gap:16px;gap:16px}}@media (min-width: 1024px){.List{grid-template-columns:repeat(3, 1fr);grid-gap:29px;gap:29px}}

.ArticleGet--first-load{min-height:100vh;display:flex;align-items:center;justify-content:center}.ArticleGet__content{flex-direction:column;align-items:center}.ArticleGet__title{padding:140px 0 50px;font-size:var(--fs-h1);font-weight:var(--fw-normal);color:var(--black);text-align:center;margin:0}@media (max-width: 768px){.ArticleGet__title{max-width:320px}}.ArticleGet__button-container{display:flex;flex-direction:column;align-items:center;padding-top:54px;padding-bottom:50px}@media (min-width: 1024px){.ArticleGet{min-height:0;display:block;align-items:initial}.ArticleGet__button-container{padding-top:50px}}

.Loader{opacity:0;height:0px;display:flex;align-items:center;justify-content:center;transition:all 0.2s linear}.Loader--isLoading{opacity:1;height:100px;transition:all 0.5s linear}

.Input{width:100%;display:block;position:relative;padding:6px 0 15px;box-sizing:border-box;cursor:pointer}@media (min-width: 450px){.Input{max-width:380px}}.Input__label{margin:0;box-sizing:border-box;font-size:var(--fs-sm);color:var(--field-helper-text);position:absolute;top:0;left:14px;background-color:transparent;padding:0 3px;cursor:pointer}.Input__input{width:100%;border:1px solid var(--field-border-enabled);height:54px;padding:14px 16px;border-radius:var(--field-radii);box-sizing:border-box;background-color:transparent;color:var(--black);outline:none}.Input__input:focus,.Input__input:active{border:1px solid var(--field-border-enabled)}.Input__error-container{position:absolute;z-index:5000;top:62px}.Input__error{margin:0;box-sizing:border-box;font-size:var(--fs-sm);line-height:var(--lh-sm);color:var(--error-color);background-color:transparent;padding:0 16px;cursor:pointer;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;overflow-wrap:break-word}

.Form{display:flex;flex-direction:column;align-items:center}

.ArticlePost__title{padding:140px 0 50px;font-size:var(--fs-h1);font-weight:var(--fw-normal);color:var(--black);text-align:center;margin:0}@media (max-width: 768px){.ArticlePost__title{max-width:320px}}


/* @import url('./reset.css');  */
/* drop styles button */

:root {
  /* Typography */
  --family: 'Nunito', sans-serif;
  --fs-sm: 12px;
  --fs-md: 16px;
  --fs-h1: 40px;

  --fw-normal: 400;
  --lh-sm: 14px;
  --lh-md: 26px;
  --lh-h1: 40px;

  /* Other */
  --yellow: #F4E041;
  --blue: #00BDD3;
  --light-grey: #F8F8F8;
  --bg-color: #E5E5E5;
  --white: #FFFFFF;
  --black: rgba(0, 0, 0, 0.87);
  
  /* UI */
  --button-color: rgba(0, 0, 0, 0.87);
  --button-primary-bg: #F4E041;
  --button-hover-bg: #FFE302;
  --button-disabled-bg: #B4B4B4;
  --button-disabled-color: rgba(255, 255, 255, 0.87);
  --button-br: 80px;
  --button-radii: 4px;
  
  --field-border-enabled: #D0CFCF;
  --field-radii: 4px;
  --field-helper-text: #7E7E7E;
  --field-border-error: rgba(203, 61, 64, 1);
  --error-color: #CB3D40;

  --card-br: 10px;
}

* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  font-family: var(--family);
  font-size: 16px;
  font-size: var(--fs-md);
  margin: 0;
  padding: 0;
}

body {
  background-color: #E5E5E5;
  background-color: var(--bg-color);
  min-width: 360px;
  overflow-x: hidden;
}

